.darken {
  position: absolute;
  left: 0px;
  top: 0px;

  height: 100%;
  width: 100%;

  background: #fafafa;
  opacity: 0.5;
}

.modal {
  position: absolute;
  top: 50%;
  left: calc(50% - 110px);

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;

  width: 250px;

  box-sizing: border-box;

  border: 1px solid;
  border-radius: 5px;
  background: #fafafa;
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;

  height: 100%;
  width: 100%;

  margin: 0 auto;
  padding: 5%;
  box-sizing: border-box;
}

.text {
  text-align: center;
  text-transform: lowercase;
  padding-bottom: 10%;
}

.text::first-letter {
  text-transform: uppercase;
}
