.field-container {
  display: inline-flex;
  width: 100%;

  margin: 3% 0%;
}

.field-container > :nth-child(1) {
  width: 30%;
}

.field-container > :nth-child(2) {
  width: 70%;
}

.error {
  border: 1px solid #ff0000;
}

.error-tooltip {
  font-size: 0.7rem;
  color: #ff0000;
}

.input-container {
  display: flex;
  flex-direction: column;
}

.field-input {
  height: 24px;

  padding: 1% 5%;
  outline: none;
}
