/* Работает в Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #F0F0F0;
}

/* Работает в Chrome, Edge и Safari */
*::-webkit-scrollbar {
  width: 6px;
  height: 10px;
}

*::-webkit-scrollbar-track {
  background: #F0F0F0;
}

*::-webkit-scrollbar-thumb {
  border-radius: 3px;
  border: 1px solid;
}