.darken {
  position: absolute;
  left: 0px;
  top: 0px;

  height: 100%;
  width: 100%;

  background: #fafafa;
  opacity: 0.5;
}

.loader {
  position: absolute;
  top: 40%;
  left: calc(50% - 110px);

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;

  width: 220px;

  margin: 0 auto;
  padding: 1%;

  border: 1px solid;
  border-radius: 5px;
  background: #fafafa;
}

.waiter {
  height: 20px;
  width: 20px;

  border: 2px dashed;
  border-radius: 50%;

  animation: rotate 2s infinite linear;
}

.info {
  width: 80%;

  text-align: center;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
