.about {
    position: relative;
    top: 20vh;

    display: flex;
    flex-direction: column;
    align-items: center;

    width: 50%;
    min-width: 300px;

    margin: 0 auto;
    padding: 1%;

    border: 1px solid;
    border-radius: 5px;
}

.about__info {
    padding: 0px 10px;

    text-align: justify;
    word-wrap: break-word;
}

.about__link {
    margin: 15px 0px;
}