.button {
  height: 32px;
  width: 50%;
  max-width: 200px;

  margin: 1%;
}

.button:hover {
  cursor: pointer;
}

.button:disabled {
  cursor: default;
}

.button.small {
  max-width: 100px;
}

.button.fixed {
  min-width: 140px;
}

@media screen and (max-height: 500px) {
  .button {
    height: 23px;
  }
}

@media screen and (width < 460px) {
  .button {
    width: auto;
  }
}